/* startpage.css */

.startpage {
    width: 100%;
    height: 100vh;
    display: flex;
    justify-content: center;
    align-items: center;
    overflow: hidden;
}

.image{

    width: 100%;
    height: 70vh;
    object-fit: cover;
    position: absolute;
    top: 0;
    left: 0;
    z-index: -1;
}

.hero-text {
    position: absolute;
    text-align: center;
    color: white;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
}

.hero-text h1 {
    font-size: 3rem;
}

.hero-text h1 span {
    color: #ff5733;
}

.hero-text p {
    font-size: 1.5rem;
    margin-top: 1rem;
}

.hero-text button {
    background-color: #ff5733;
    border: none;
    color: white;
    padding: 10px 20px;
    font-size: 1rem;
    margin-top: 1.5rem;
    cursor: pointer;
    border-radius: 5px;
}

  .hero-text button:hover {
    background-color: #ff2e00;
  }
  .title-par{
color: white;
  }