.footer-container {
    color: white;
    padding: 20px 0;
    background-color: #101010;
     /* Adds space between content and footer */
}

/* Align content horizontally for larger screens */
.footer {
    display: flex;
    flex-direction: row;
    justify-content: space-around; /* Space out items */
    align-items: flex-start; /* Align items from the top */
    padding: 20px 50px;
    gap: 150px; /* Adjust the space between Links and Contact */
}

/* Center content on smaller screens and stack vertically */
@media (max-width: 768px) {
    .footer {
        flex-direction: column; /* Stack items vertically */
        justify-content: center; /* Center items vertically */
        align-items: center; /* Center items horizontally */
        gap: 30px; /* Adjust gap for mobile screens */
        padding: 20px;
    }

    .footer-links, .footer-social {
        width: auto; /* Adjust width for mobile view */
        align-items: center; /* Center both sections */
    }
}

.footer-links, .footer-social {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center; /* Center the content */
    text-align: center;
}

.footer-links {
    margin-left: 0; /* Remove margins for mobile view */
}

.footer-social {
    margin-right: 0; /* Remove margins for mobile view */
}

.footer-links h2, .footer-social h2 {
    margin-bottom: 15px; /* Adds space below the section title */
    text-align: center; /* Align the text to the center */
}

.footer-links ul {
    list-style: none;
    padding: 0;
    margin: 0;
    display: flex;
    flex-direction: column;
    align-items: center; /* Center links */
}

.footer-links ul li {
    margin: 10px 0; /* Increase vertical spacing between links */
}

.footer-links ul li:hover {
    /* Add a subtle hover effect */
    transform: translateY(-3px) scale(1.1);
    transition: all 0.3s ease;
}

.footer-links ul li a {
    color: #fff;
    text-decoration: none;
    font-size: 16px;
}

.footer-links ul li a:hover {
    text-decoration: underline;
    color: #ff5733;
}

/* Social links styles */
/* Social links styles */
.footer-social a {
    text-decoration: none;
    display: flex;
    justify-content: flex-start; /* Align items horizontally to the start */
    align-items: center; /* Align items vertically */
    margin: 10px 0; /* Increase vertical spacing between social icons */
    line-height: 1.5rem; /* Add line-height for better alignment */
    width: 100%; /* Ensure full width for alignment consistency */
}

/* Ensure the text is aligned properly with icons */
.footer-social a svg {
    margin-right: 8px; /* Space between the icon and the text */
    flex-shrink: 0; /* Prevent the icon from shrinking */
    display: inline-block;
    vertical-align: middle; /* Align the icon with the middle of the text */
}

/* Add consistent font size for social links */
.footer-social a span {
    font-size: 16px;
    color: #fff; /* Ensure text is visible */
    line-height: 1.5rem; /* Adjust line-height for proper alignment */
}

.footer-social a:hover {
    opacity: 0.8;
    transform: translateY(-3px) scale(1.1);
    transition: all 0.3s ease;
}


p {
    margin-top: 20px;
    font-size: 14px;
    text-align: center;
}
