/* Updated navbar.css with uniform borders */

* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

.navbar-container {
    font-size: 62.5%;
    font-family: 'Roboto', sans-serif;
}

li {
    list-style: none;
}

a {
    text-decoration: none;
}

/* Main navbar sidebar */
.navbar {
    position: fixed;
    top: 0;
    left: -250px; /* Start completely off-screen */
    width: 250px;
    height: 100vh;
    background-color: #101010;
    z-index: 1000;
    transition: left 0.3s ease-in-out;
    box-shadow: 2px 0 5px rgba(0, 0, 0, 0.5); /* Add shadow for depth */
}

/* Ensure navbar is fully hidden on mobile */
@media only screen and (max-width: 768px) {
    .navbar {
        left: -100%; /* Ensure it's completely off the screen on mobile */
        width: 100%;
    }
}

/* When the navbar is active, it slides in */
.navbar.active {
    left: 0; /* Bring the navbar fully into view */
}

/* Hamburger button */
.hamburger {
    display: block;
    position: fixed;
    top: 20px;
    left: 10px; /* Initially positioned on the left */
    cursor: pointer;
    z-index: 20;
    transition: left 0.3s ease-in-out;
}

.hamburger.open {
    left: 260px; /* Push the hamburger to the right when the navbar is open */
}

.bar {
    display: block;
    width: 25px;
    height: 3px;
    margin: 5px auto;
    transition: all 0.3s ease-in-out;
    background-color: #101010;
}

.hamburger.open .bar:nth-child(1) {
    transform: rotate(45deg) translate(5px, 5px);
}

.hamburger.open .bar:nth-child(2) {
    opacity: 0;
}

.hamburger.open .bar:nth-child(3) {
    transform: rotate(-45deg) translate(5px, -5px);
}

/* Sidebar open/close behavior */
.nav-menu {
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: flex-start;
    position: fixed;
    left: -250px; /* Fully off-screen by default */
    top: 0;
    height: 100vh;
    width: 250px;
    background-color: #101010; /* Dark background for menu */
    transition: left 0.3s ease-in-out;
}

/* Slide the nav-menu in when it's active */
.nav-menu.active {
    left: 0;
}

/* Updated nav-item with consistent border design */
.nav-item {
    margin: 1.5rem 0; /* Add consistent vertical spacing */
    padding: 1rem 0;
    border-top: 2px solid #ff5733; /* Consistent border thickness and color */
    text-align: center;
    width: 100%; /* Make sure the border covers the full width */
}

.nav-item:first-child {
    border-top: none; /* Remove the border for the first item */
}

.nav-link {
    font-size: 1.8rem;
    font-weight: 400;
    color: #ffffff;
    letter-spacing: 0.05em; /* Add letter spacing for better readability */
    transition: color 0.3s ease, transform 0.3s ease;
}

.nav-link:hover {
    color: #ff5733;
    transform: scale(1.05); /* Slight scale-up effect */
}

/* Hides horizontal overflow */
html, body {
    overflow-x: hidden;
}

/* Fullscreen content when the sidebar is closed */
body .startpage {
    margin-left: 0;
    width: 100%; /* Full width when sidebar is closed */
}

/* When menu is open, limit the width of the content */
body.menu-open .startpage {
    margin-left: 250px; /* Push content to the right when the sidebar is open */
    width: calc(100% - 250px); /* Shrink content */
}

/* Adjust responsiveness for mobile screens */
@media only screen and (max-width: 768px) {
    .navbar {
        left: -100%; /* Fully hide navbar on mobile */
        width: 100%; /* Full width for mobile */
    }

    .nav-menu {
        left: -100%; /* Fully hide the menu off-screen for mobile */
        width: 100%; /* Full width for mobile menu */
    }

    .nav-menu.active {
        left: 0; /* Slide-in effect for mobile */
    }

    .hamburger {
        left: 10px;
        top: 15px;
    }

    body.menu-open .startpage {
        margin-left: 0;
        width: 100%; /* On mobile, content takes full width */
    }
}
