.about-container {
    display: flex;
    flex-direction: row;
    height: 100vh; /* Full viewport height */
    align-items: stretch; /* Stretch both sections to full height */
}

.about-content {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    background-color: white;
    width: 70%;
    padding: 20px;
    padding-top: 60px; /* Push down content to avoid overlapping with navbar */
}

/* Header for About Section */
header h1 {
    font-size: 4rem;
    color: #ff5733;
    margin-bottom: 20px;
}

.about-image {
    width: 150px;
    height: 150px;
    border-radius: 50%;
    object-fit: cover;
    margin: 20px 0;
}

.text-section {
    font-size: 1.2rem;
    background-color: #e0e0e0;
    padding: 20px;
    border-radius: 10px;
    width: 60%;
    text-align: center;
}

/* Right Section */
.right-section {
    background-color: #101010;
    width: 30%;
    display: flex;
    flex-direction: row; /* Stacks content vertically */
    justify-content: space-evenly;
    align-items: flex-start;
    padding: 20px;
    height: 100%; /* Ensure full height */
}

/* Align header row content */
.header-row {
    display: flex;
    align-items: center;
    flex-direction: row;
    gap: 10px; /* Add space between the icon and the text */
}
.knowledge-content , .language-section {
    margin-top: 20px;
}
/* Knowledge Content */
.knowledge-content ul {
    list-style-type: none;
    padding: 0;
}

.knowledge-content ul li {
    font-size: 1.3rem;
    color: #FFFF;
    padding: 10px 0;
}

/* Skill bar styles */
.skill-bar {
    background-color: #ddd;
    border-radius: 10px;
    overflow: hidden;
    width: 100%;
    height: 15px;
    margin: 5px 0;
}

.skill-level {
    height: 100%;
    background-color: #ff5733;
    text-align: right;
    padding-right: 5px;
    color: white;
    border-radius: 10px;
}

/* Icon styling */
.header-row .icon {
    color: #FFFF;
    font-size: 3rem; /* Adjust the size of the icon as needed */
}

/* Language Section */
.language-section ul {
    list-style-type: none;
    padding: 0;
    margin-top: 20px;
}

.language-section ul li {
    font-size: 1.3rem;
    color: #fff;
    padding: 10px 0;
}

/* Media Query for Tablet Screens */
@media screen and (max-width: 768px) {
    .about-container {
        flex-direction: column;
        align-items: center;
        height: auto;
    }

    .about-content,
    .right-section {
        width: 100%;
        height: auto;
    }

    header h1 {
        font-size: 3rem;
    }

    .about-image {
        width: 120px;
        height: 120px;
    }

    .text-section {
        font-size: 1rem;
        width: 80%;
    }

    .knowledge-content ul li {
        font-size: 1.2rem;
    }

    .skill-bar {
        height: 12px;
    }
}

/* Media Query for Mobile Screens */
@media screen and (max-width: 480px) {
    .about-container {
        flex-direction: column;
        align-items: center;
        height: auto;
    }

    .about-info {
        margin-top: 70px;
    }

    .about-content,
    .right-section {
        width: 100%;
        padding: 10px;
        margin-top: 20px;
    }

    header h1 {
        font-size: 2.5rem;
    }

    .about-image {
        width: 100px;
        height: 100px;
    }

    .text-section {
        font-size: 0.9rem;
        width: 90%;
    }

    .knowledge-content ul li {
        font-size: 1rem;
    }

    .skill-bar {
        height: 10px;
    }
}
